"use client"

import { useEffect, useState, useRef } from "react"
import { useRouter } from "next/navigation"
import { Button } from "@/components/ui/button"
import { ConversationList } from "@/components/conversation-list"
import { motion } from "framer-motion"
import { storage } from "@/lib/storage"
import { createClient } from "@/lib/supabase/client"
import type { AuthUser, Conversation } from "@/lib/types"
import Link from "next/link"
import { LogOut, Settings } from "lucide-react"
import { ThemeToggle } from "@/components/theme-toggle"
import { Avatar } from "@/components/avatar"

export default function DashboardPage() {
  const router = useRouter()
  const [auth, setAuth] = useState<AuthUser | null>(null)
  const [conversations, setConversations] = useState<Conversation[]>([])
  const [loading, setLoading] = useState(true)
  const supabaseRef = useRef(createClient())

  useEffect(() => {
    const loadAuth = async () => {
      const authUser = await storage.getAuth()
      if (!authUser) {
        router.push("/login")
        return
      }
      setAuth(authUser)
      await loadConversations(authUser.id)
    }
    loadAuth()
  }, [router])

  // Set up real-time WebSocket subscription for conversation updates
  useEffect(() => {
    if (!auth?.id) return

    // Subscribe to all messages for this business's conversations
    const channel = supabaseRef.current
      .channel(`business:${auth.id}`, {
        config: {
          broadcast: { self: true },
        },
      })
      .on(
        "postgres_changes",
        {
          event: "INSERT",
          schema: "public",
          table: "messages",
        },
        async (payload) => {
          // Reload conversations to update the list
          const convs = await storage.getConversationsByBusinessId(auth.id)
          setConversations(convs)
        }
      )
      .on(
        "postgres_changes",
        {
          event: "UPDATE",
          schema: "public",
          table: "conversations",
        },
        async (payload) => {
          // Reload conversations when conversation is updated
          const convs = await storage.getConversationsByBusinessId(auth.id)
          setConversations(convs)
        }
      )
      .subscribe((status) => {
        console.log("Dashboard WebSocket subscription status:", status)
      })

    return () => {
      supabaseRef.current.removeChannel(channel)
    }
  }, [auth?.id])

  const loadConversations = async (businessId: string) => {
    const convs = await storage.getConversationsByBusinessId(businessId)
    setConversations(convs)
    setLoading(false)
  }

  const handleLogout = async () => {
    await storage.clearAuth()
    router.push("/")
  }

  if (!auth) return null

  return (
    <main className="h-screen flex flex-col md:flex-row bg-[var(--chat-bg)] dark:bg-[var(--chat-bg)]">
      {/* Sidebar */}
      <motion.aside
        initial={{ opacity: 0, x: -20 }}
        animate={{ opacity: 1, x: 0 }}
        className="w-full md:w-80 border-r border-[#313d45] md:border-border flex flex-col bg-[#111b21] md:bg-card"
      >
        {/* Header */}
        <div className="p-3 md:p-4 border-b border-[#313d45] md:border-border bg-[#202c33] md:bg-transparent">
          <div className="flex items-center justify-between mb-3 md:mb-4">
            <h1 className="text-xl md:text-2xl font-bold text-[#e9edef] md:text-primary">Leenk</h1>
            <div className="flex gap-1 md:gap-2">
              <Link href="/dashboard/settings">
                <Button variant="ghost" size="sm" className="text-[#aebac1] hover:text-[#e9edef] hover:bg-[#2a3942]">
                  <Settings className="w-4 h-4" />
                </Button>
              </Link>
              <Button variant="ghost" size="sm" onClick={handleLogout} className="text-[#aebac1] hover:text-[#e9edef] hover:bg-[#2a3942]">
                <LogOut className="w-4 h-4" />
              </Button>
            </div>
          </div>
          <div className="text-sm">
            <p className="font-medium text-[#e9edef] md:text-foreground">{auth.business?.businessName}</p>
            <p className="text-[#8696a0] md:text-muted-foreground text-xs">{auth.email}</p>
          </div>
        </div>

        {/* Conversations List */}
        <div className="flex-1 overflow-y-auto bg-[#0b141a] md:bg-transparent">
          {loading ? (
            <div className="flex items-center justify-center h-full">
              <p className="text-[#8696a0] md:text-muted-foreground">Loading conversations...</p>
            </div>
          ) : (
            <ConversationList conversations={conversations} />
          )}
        </div>

        {/* Share Link */}
        {auth.business?.phone && (
          <div className="p-3 md:p-4 border-t border-[#313d45] md:border-border bg-[#111b21] md:bg-secondary/20">
            <p className="text-xs text-[#8696a0] md:text-muted-foreground mb-2">Share this link with customers:</p>
            <code className="text-xs bg-[#202c33] md:bg-input p-2 rounded block truncate text-[#e9edef] md:text-foreground">/chat/{auth.business.phone}</code>
          </div>
        )}
      </motion.aside>

      {/* Main Content */}
      <motion.div
        initial={{ opacity: 0 }}
        animate={{ opacity: 1 }}
        className="hidden md:flex flex-1 items-center justify-center p-4"
      >
        <div className="text-center">
          <h2 className="text-2xl font-bold mb-2">Select a Conversation</h2>
          <p className="text-muted-foreground">Click on a conversation to start chatting with your customers</p>
        </div>
      </motion.div>
    </main>
  )
}
